<template>
  <el-dialog
    :title="title"
    top="5vh"
    :visible="value"
    :show-close="true"
    width="1000px"
    :lock-scroll="false"
    :destroy-on-close="true"
    :append-to-body="true"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <el-scrollbar class="scroll-dialog">
      <div class="main-box">
        <el-form ref="filterItem" class="top-filter clearfix" :model="query" inline @submit.native.prevent>
          <el-form-item prop="materialCodeOrName">
            <el-input v-model.trim="query.materialCodeOrName" placeholder="物料编码或名称" class="filter-item" style="width: 178px;" clearable @keyup.enter.native="searchInit" />
          </el-form-item>
          <el-button
            class="filter-item"
            type="success"
            icon="el-icon-search"
            @click="searchInit"
          >搜索</el-button>
          <el-button
            class="filter-item"
            type="warning"
            icon="el-icon-refresh-left"
            @click="resetForm('filterItem')"
          >重置</el-button>
        </el-form>
        <el-row>
          <table-com
            :ref="refName"
            :row-key="rowKey"
            :table-height-type="490"
            :loading="loading"
            :index="true"
            :index-fixed="false"
            :selection="true"
            :multiple="multiple"
            :selection-fixed="false"
            :table-column-sort="false"
            :table-data="tableData"
            :column-list="columnList"
            :show-operate="false"
            :operate-fixed="false"
            v-bind="$attrs"
            :table-selection="tableSelection"
            :selectable="selectable"
            @selectionChange="selectionChange"
            @sortChange="sortChange"
            @dynamicRow="getDynamicRow"
          />
        </el-row>
        <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />
      </div>
    </el-scrollbar>
    <div slot="footer">
      <el-row v-if="multiple" style="text-align: left;">
        <el-button type="primary" :disabled="!tableSelection.length" @click="handleSelectUp">向上选中</el-button>
        <el-button type="primary" :disabled="!tableSelection.length" @click="handleCancelUp">向上取消</el-button>
        <el-button type="primary" :disabled="!tableSelection.length" @click="handleSelectDown">向下选中</el-button>
        <el-button type="primary" :disabled="!tableSelection.length" @click="handleCancelDown">向下取消</el-button>
      </el-row>
      <el-button type="primary" @click="handleConfirm">确 定</el-button>
      <el-button @click="beforeClose">取 消</el-button>
    </div>
  </el-dialog>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
import { getListApi as getListApi } from '@/apis/modules/base/baseMaterialInformation'

export default {
  name: 'MaterialSelect',
  mixins: [paginationPage],
  props: {
    value: {
      required: true,
      type: Boolean
    },
    title: {
      type: String,
      default: '物料选择'
    },
    // 是否多选，如果多选，返回id的数组， 如果单选返回 {id, name}
    multiple: {
      type: Boolean,
      default: true
    },
    // 默认勾选的ids
    selection: {
      type: Array,
      default: () => []
    },
    // 多选状态下的最大选择数量
    max: {
      type: Number,
      default: 0
    },
    ids: {
      type: Array,
      default: () => []
    },
    extraInfo: {
      type: Object,
      default: () => {}
    }
  },
  dicts: [],
  data() {
    return {
      loading: false,
      refName: 'modelSelectMaterialSelect',
      query: {
        materialCodeOrName: ''
      },
      tableData: [],
      columnList: [
        // {
        //   module: 'number',
        //   prop: 'deliveryNumber',
        //   label: '本次到货数量',
        //   align: 'center',
        //   minWidth: 160,
        //   fixed: false,
        //   sortable: false,
        //   resizable: true,
        //   formatter: null,
        //   headerAlign: 'center',
        //   setMin: (row) => {
        //     return 0
        //   },
        //   setPrecision: (row) => {
        //     return 3
        //   }
        //   // setMax: (row) => {
        //   //   return row.orderNum
        //   // }
        // },
        {
          module: 'word',
          prop: 'code',
          label: '物料编号',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'name',
          label: '物料名称',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'spec',
          label: '规格型号',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'assessType_dictText',
          label: '评估类型',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'description',
          label: '物料描述',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'unitMeasureName',
          label: '计量单位',
          align: 'center',
          minWidth: 175,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'type_dictText',
          label: '物料类型',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'minOutboundUnitName',
          label: '最小单位SKU',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'isExpirationDate',
          label: '是否有保质期',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center',
          formatter: (c) => c.isExpirationDate === 0 ? '否' : '是'
        },
        {
          module: 'word',
          prop: 'materialGroupCode',
          label: '物料组编号',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialGroupDescription',
          label: '物料组描述',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'dataSources_dictText',
          label: '数据来源',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center'
        }
      ],
      columnSets: [],
      tableSelection: [],
      deptSelectedInfo: {
        list: [],
        visible: false,
        radio: ''
      },
      rowObj: {},
      rowKey: 'id'
    }
  },
  methods: {
    init() {
      const params = {
        ...this.query,
        ...this.extraInfo,
        page: {
          ...this.pageInfo
        }
      }
      return new Promise((resolve, reject) => {
        getListApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { list, ...pageInfo } = data
            this.initPage(pageInfo)
            this.tableData = list.map(i => ({ deliveryNumber: 0, ...i }))
            try {
              const ids = this.tableSelection.map(i => i[this.rowKey])
              // 设置操作项开始
              const curPageCheckeds = this.tableData.filter(c => ids.includes(c.id)) // 当前页选中项
              this.rowObj = curPageCheckeds.length ? curPageCheckeds[curPageCheckeds.length - 1] : {} // 初始化设置最后一个选中项为操作项
              // 设置操作项结束
              const rowKey = this.rowKey
              this.tableData.forEach(row => {
                function check(row) {
                  const id = row[rowKey]
                  return ids.some(i => i === id)
                }
                if (check(row)) {
                  this.$nextTick(() => {
                    this.$refs[this.refName].$refs.multipleTable.toggleRowSelection(row, true)
                  })
                }
              })
            } catch (error) {
              console.log('捕获错误：', error)
            }
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    // 表格自定义排序
    sortChange({ column, prop, order }) {
      // this.init()
    },
    // 重置
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.searchInit()
    },
    // 初始化
    initSelect() {
      this.searchInit()
    },
    // 选项改变
    selectionChange(arr) {
      this.tableSelection = [...arr]
    },
    // 当用户手动勾选数据行的 Checkbox 时触发的事件的回调，参数为当前勾选行
    getDynamicRow(item) {
      this.rowObj = item
    },
    // 向上选中
    handleSelectUp() {
      // 所有选中项
      const ids = this.tableSelection.map(i => i[this.rowKey])
      // 当前页选中项
      const curPageCheckeds = this.tableData.filter(c => ids.includes(c.id))
      let ind = -1 // 当前操作项索引位置
      let prev = [] // 向上的数据
      if (Object.keys(this.rowObj).length) { // 证明当前页存在操作项
        if (!ids.includes(this.rowObj.id)) { // 证明当前页操作项取消了勾选
          ind = this.tableData.findIndex(c => c.id === curPageCheckeds[curPageCheckeds.length - 1].id)
        } else {
          ind = this.tableData.findIndex(c => c.id === this.rowObj.id)
        }
      }
      this.tableData.forEach((item, index, arr) => {
        if (ind !== -1) {
          if (index < ind) {
            prev = [...prev, item]
          }
          this.rowObj = arr[ind] // 重新设置操作项
        }
      })
      this.$nextTick(() => {
        prev.forEach(c => {
          !ids.includes(c.id) && this.$refs[this.refName].$refs.multipleTable.toggleRowSelection(c, true)
        })
      })
    },
    // 向上取消
    handleCancelUp() {
      // 所有选中项
      const ids = this.tableSelection.map(i => i[this.rowKey])
      // 当前页选中项
      const curPageCheckeds = this.tableData.filter(c => ids.includes(c.id))
      let ind = -1 // 当前操作项索引位置
      let prev = [] // 向上的数据
      if (Object.keys(this.rowObj).length) { // 证明存在操作项
        if (!ids.includes(this.rowObj.id)) { // 证明操作项取消了勾选
          ind = this.tableData.findIndex(c => c.id === curPageCheckeds[curPageCheckeds.length - 1].id)
        } else {
          ind = this.tableData.findIndex(c => c.id === this.rowObj.id)
        }
      }
      this.tableData.forEach((item, index, arr) => {
        if (ind !== -1) {
          if (index < ind) {
            prev = [...prev, item]
          }
          this.rowObj = arr[ind] // 重新设置操作项
        }
      })
      this.$nextTick(() => {
        prev.forEach(c => {
          ids.includes(c.id) && this.$refs[this.refName].$refs.multipleTable.toggleRowSelection(c, false)
        })
      })
    },
    // 向下选中
    handleSelectDown() {
      // 所有选中项
      const ids = this.tableSelection.map(i => i[this.rowKey])
      // 当前页选中项
      const curPageCheckeds = this.tableData.filter(c => ids.includes(c.id))
      let ind = -1 // 当前操作项索引位置
      let next = [] // 向下的数据
      if (Object.keys(this.rowObj).length) { // 证明存在操作项
        if (!ids.includes(this.rowObj.id)) { // 证明操作项取消了勾选
          ind = this.tableData.findIndex(c => c.id === curPageCheckeds[curPageCheckeds.length - 1].id)
        } else {
          ind = this.tableData.findIndex(c => c.id === this.rowObj.id)
        }
      }
      this.tableData.forEach((item, index, arr) => {
        if (ind !== -1) {
          if (index > ind) {
            next = [...next, item]
          }
          this.rowObj = arr[ind] // 重新设置操作项
        }
      })
      this.$nextTick(() => {
        next.forEach(c => {
          !ids.includes(c.id) && this.$refs[this.refName].$refs.multipleTable.toggleRowSelection(c, true)
        })
      })
    },
    // 向下取消
    handleCancelDown() {
      // 所有选中项
      const ids = this.tableSelection.map(i => i[this.rowKey])
      // 当前页选中项
      const curPageCheckeds = this.tableData.filter(c => ids.includes(c.id))
      let ind = -1 // 当前操作项索引位置
      let next = [] // 向下的数据
      if (Object.keys(this.rowObj).length) { // 证明存在操作项
        if (!ids.includes(this.rowObj.id)) { // 证明操作项取消了勾选
          ind = this.tableData.findIndex(c => c.id === curPageCheckeds[curPageCheckeds.length - 1].id)
        } else {
          ind = this.tableData.findIndex(c => c.id === this.rowObj.id)
        }
      }
      this.tableData.forEach((item, index, arr) => {
        if (ind !== -1) {
          if (index > ind) {
            next = [...next, item]
          }
          this.rowObj = arr[ind] // 重新设置操作项
        }
      })
      this.$nextTick(() => {
        next.forEach(c => {
          ids.includes(c.id) && this.$refs[this.refName].$refs.multipleTable.toggleRowSelection(c, false)
        })
      })
    },
    // 关闭
    beforeClose(done) {
      this.$emit('input', false)
      typeof done === 'function' && done()
      this.tableSelection = new Array(0)
      this.$refs['filterItem'].resetFields()
    },
    // 确定
    handleConfirm() {
      this.$emit('confirm', this.tableSelection)
      this.beforeClose()
    },
    // 注意：此方法貌似废弃了
    onDeptSelectConfirm(list) {
      if (list.length) {
        this.query.usedOffice = list[0].name
      }
    },
    // Tip 控制表格某行是否可被勾选
    selectable(row, index) {
      // this.selection 之前勾选的项
      return !this.selection.includes(row.id) // 选过就禁止再选
    }
  }
}
</script>

<style lang="scss" scoped>
.scroll-dialog {
  height: 600px;
}
.main-box {
  padding: 10px;
  box-sizing: border-box;
}
::v-deep .el-scrollbar .el-scrollbar__wrap {
  overflow-x: hidden;
}
 ::v-deep .el-dialog__body {
  padding-top: 10px;
  padding-bottom: 0;
}
</style>
