import request from '@/apis/request.wms'
// import download from '@/apis/request.download'

// 获取树列表
export function getListApi(data = {}) {
  return request({
    url: '/basic/supplier/supplierMaterialPageList',
    method: 'post',
    data
  })
}

export function deleteApi(data = {}) {
  return request({
    url: '/basic/supplier/delete',
    method: 'post',
    data
  })
}
