<template>
  <div class="app-container">
    <div class="clearfix top-filter">
      <el-form ref="filterItem" :model="query" inline @submit.native.prevent>
        <el-form-item prop="materialId">
          <template>
            <div class="selected-wrapper">
              <el-input v-model.trim="query.materialCode" disabled placeholder="请选择物料" style="width: 143px;" />
              <el-button type="primary" style="margin-left:5px;padding-left:0;padding-right:0;width:30px" icon="el-icon-search" @click="visible = true" />
            </div>
          </template>
        </el-form-item>
        <el-button
          :disabled="!query.materialId"
          class="filter-item"
          type="success"
          icon="el-icon-search"
          @click="searchInit"
        >搜索</el-button>
        <el-button
          :disabled="!query.materialId"
          class="filter-item"
          type="warning"
          icon="el-icon-refresh-left"
          @click="resetForm('filterItem')"
        >重置</el-button>
        <!-- <el-button
          class="filter-item"
          type="primary"
          icon="el-icon-plus"
          @click="handleAdd"
        >新增</el-button> -->
      </el-form>
    </div>
    <el-row>
      <table-com
        ref="baseTableRef"
        :loading="loading"
        :selection="false"
        :table-data="tableData"
        :column-list="columnList"
        :operation-width="180"
        :show-operate="false"
        :table-column-sort="false"
        :show-empty-text-slot="true"
        @rowClick="handleRowClick"
        @refresh="searchInit"
      >
        <template #empty-slot>
          <span class="my-empty-text">暂无数据</span>
        </template>
        <template slot-scope="scope">
          <el-button type="warning" icon="el-icon-edit" @click="handleModify(scope.row)">编辑</el-button>
          <el-button type="danger" icon="el-icon-delete" @click="handleDelete(scope.row)">删除</el-button>
        </template>
      </table-com>
    </el-row>
    <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />
    <base-material
      v-model="visible"
      :multiple="false"
      :extra-info="{...query.materialId && {materialIdList: [query.materialId]} }"
      @confirm="handleChooseMaterial"
    />
  </div>
</template>

<script>
import { getListApi, deleteApi } from '@/apis/modules/in-storage/materialSupplier'
import paginationPage from '@/mixins/paginationPage'
import BaseMaterial from './MaterialChoose'

export default {
//   name: 'InStorageMaterialSupplier',
  components: {
    BaseMaterial
  },
  dicts: [],
  mixins: [paginationPage],
  data() {
    return {
      query: {
        materialId: '',
        materialCode: '',
        materialName: ''
      },
      dialogForm: {},
      title: this.$route.meta.title,
      loading: false,
      tableData: [],
      dialogType: 'add',
      dialogVisible: false,
      columnList: [
        {
          module: 'word',
          prop: 'supplierName',
          label: '供应商名称',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'supplierCode',
          label: '供应商编号',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialCount',
          label: '历史数量', // 历史数量（该供应商一共送过多少这个物料)
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'contractCount',
          label: '合同数', // 合同数（一共多少个合同）
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'orderCount',
          label: '订单数', // 订单数（一共多少个订单）
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'lastOrderTime',
          label: '最终签订订单时间',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ],
      visible: false
    }
  },
  computed: {
    dialogTitle() {
      if (this.dialogType === 'add') {
        return `新增${this.title}`
      } else {
        return `编辑${this.title}`
      }
    }
  },
  watch: {
    $route: {
      handler({ params }) {
      },
      immediate: true
    }
  },
  mounted() {
    // this.init()
  },
  activated() {
    // this.init()
  },
  methods: {
    init() {
      this.loading = true
      return new Promise((resolve, reject) => {
        getListApi({
          page: {
            ...this.pageInfo
          },
          materialId: this.query.materialId
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { list, ...pageInfo } = data
            this.initPage(pageInfo)
            this.tableData = list
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    handleAdd() {
      this.dialogType = 'add'
      this.dialogForm = {}
      this.dialogVisible = true
    },
    handleModify(item) {
      this.dialogForm = { ...item }
      this.dialogType = 'modify'
      this.dialogVisible = true
    },
    handleDelete(item) {
      this.$confirm(`此操作将永久删除该${this.title}, 是否继续?`, '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        cancelButtonClass: 'btn-custom-cancel',
        type: 'warning',
        lockScroll: false
      }).then(() => {
        if (!item.id) return
        deleteApi({
          id: item.id,
          dataSources: item.dataSources
        }).then(res => {
          const { code } = res
          if (!code) {
            this.$message({
              message: '删除成功',
              type: 'success'
            })
            this.init()
          }
        })
      }).catch(() => {
        this.$message({
          message: '已取消删除',
          type: 'info'
        })
      })
    },
    handleRowClick({ row, column, cell, event }) {
      if (column.label === '操作') {
        return false
      }
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.query.materialId = ''
      this.query.materialName = ''
      this.query.materialCode = ''
      this.tableData = new Array(0)
    },
    handleChooseMaterial(list) {
      if (!list.length) return
      this.query.materialId = list[0].id
      this.query.materialName = list[0].name
      this.query.materialCode = list[0].code
    }
  }
}
</script>

<style scoped lang="scss">
.my-empty-text {
  font-size: 23px;
}
</style>
